import React, { Component } from "react";
import Wrapper from "./components/Wrapper";
import Title from "./components/Title";
import Rowcontainer from "./components/Rowcontainer";
import OptionBtn from "./components/OptionBtn";
import Directorycolumns from "./components/Directorycolumns";
import Employeecard from "./components/Employeecard";
import employees from "./employees.json";

class App extends Component {
    state = {
        employees
    };

    filtering = () => {
        let filtered = employees.filter(employee => employee.status === "Active");
        this.setState({ employees:filtered });

        if (this.state.employees[0].status === "Active") {
            this.reset();
            let filtered = employees.filter(employee => employee.status === "Terminated");
            this.setState({ employees:filtered });
        }
    }; 

    // sorting = () => {
    //     let sortedArray = [];
    //     let currentDirectory = this.state.employees;
        
    //     for (let i = 0; i < currentDirectory.length; i++) {
    //         sortedArray.push(employees[i].department);
    //     };
    //     sortedArray = sortedArray.sort();
    //     console.log(sortedArray);
    // };

    reset = () => {
        this.setState({ employees });
    }
        
    renderEmployees = () => {
        return (
            this.state.employees.map(employee => (
                <Employeecard
                    key={employee.id}
                    id={employee.id}
                    status={employee.status}
                    department={employee.department}
                    firstName={employee.firstName}
                    lastName={employee.lastName}
                    position={employee.position}
                    email={employee.email}
                    phone={employee.phone}
                />
            ))
        )
    };

    render() {
        return (
            <Wrapper>
                <Title>Employee Directory</Title>
                <Rowcontainer>
                    {/* <OptionBtn color="info" options={this.sorting}>
                        Sort by Departement
                    </OptionBtn> */}
                    <OptionBtn color="warning" options={this.filtering}>
                        Filter by Status
                    </OptionBtn>
                    <OptionBtn color="success" options={this.reset}>
                        Reset Directory
                    </OptionBtn>
                </Rowcontainer>
                <Directorycolumns />
                {this.renderEmployees()}
            </Wrapper>
        );
    };
};

export default App;
